@import url(https://fonts.googleapis.com/css?family-Roboto+Slab:100,300,400,700);
@import url(https://fonts.googleapis.com/css?family-Raleway:300,300i,400,400i,500,500i,600,600i);
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}
.suru {
  color: coral;
}
.heading_Style {
  padding: 30px 0;
  text-align: center;
  font-family: "Raleway",sans-serif;
  text-transform:uppercase ;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #ffff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.cards{
  width: 100%;
  height: auto;
}

.card{
  margin: 6%;
  -webkit-transform: all 0.4 cubic-bezier(0.175,0.885,0,1);
          transform: all 0.4 cubic-bezier(0.175,0.885,0,1);
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgb(0,0,0,0.1);
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0,0,0,0.1);
  -webkit-transform: scale(1.05,1.05);
          transform: scale(1.05,1.05);
}

.card_img {
  width:100%;
  height: 350px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.card_info{
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}

.card_category {
  font-family: "Raleway" ,sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 13px;
  font-weight: 500;
  color:#868686;
  background-color: #ffff;
}

.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway",sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

.button {
  padding: 5px 8px;
  font-family: "Raleway",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  color:#868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}


